<template>
  <section class="msite">
    <!-- title -->
    <!--  :title  -->
    <HeaderTop :title="address.name">
      <!-- slot="left" -->
      <router-link class="header_search" slot="left" to="/search">
        <i class="iconfont icon-sousuo"></i>
      </router-link>
      <router-link class="header_login" slot="right" :to="userInfo.id ? '/profile': '/login'">
        <span class="header_login_text" v-if="!userInfo.id">
          |
        </span>
        <span class="header_login_text" v-else>
          <i class="iconfont icon-yonghuming"></i>
        </span>
      </router-link>
    </HeaderTop>
    <!---->
    <nav class="msite_nav">
      <!-- swiperdiv -->
      <div class="swiper-container" v-if="categorys.length">
        <!-- swiperdiv -->
        <div class="swiper-wrapper">
          <!-- swiperdiv -->
          <div class="swiper-slide" v-for="(pages,index) in categorysArr" :key="index">
            <a href="javascript:" class="link_to_food" v-for="(data,index) in pages" :key="index">
              <div class="food_container">
                <img :src="baseImageUrl+data.image_url">
              </div>
              <span>{{data.title}}</span>
            </a>
          </div>
        </div>
        <!-- swiper -->
        <div class="swiper-pagination"></div>
      </div>
      <img src="./images/msite_back.svg" alt="back" v-else>
    </nav>
    <!---->
    <div class="msite_shop_list">
      <div class="shop_header">
        <i class="iconfont icon-xuanxiang"></i>
        <span class="shop_header_title"></span>
      </div>
      <ShopList></ShopList>
    </div>
    </section>
</template>

<script>
import Swiper from 'swiper'
// swipercss
import 'swiper//dist/css/swiper.min.css'

import HeaderTop from '../../components/HeaderTop/HeaderTop.vue'
import ShopList from '../../components/ShopList/ShopList.vue'
// mapStatestate
import {mapState} from 'vuex'
export default {
  data () {
    return {
      baseImageUrl: 'https://fuss10.elemecdn.com'
    }
  },
  components: {
    HeaderTop,
    ShopList
  },
  computed: {
    ...mapState(['address', 'categorys', 'userInfo']),

    /*
    categorys2
    8
    */
    categorysArr () {
      // 1.
      const {categorys} = this
      // 2.--categorysArr
      const arr = []
      // for (let i = 0, len = categorys.length; i < len; i += 8) {
      //   arr.push(categorys.slice(i, i + 8))
      // }
      // 3.--pages(8)
      let minArr = []
      // 4.categoryscatagorysArr
      categorys.forEach(data => {
        // (pages), 
        if (minArr.length === 8) {
          minArr = []
        }
        // minArr, (pages)(categorysArr)
        if (minArr.length === 0) {
          arr.push(minArr)
        }
        // (pages)
        minArr.push(data)
      })
      return arr
    }
  },
  watch: {
    categorys (value) { // categorys 
      // setTimeout, 
      /*
      setTimeout(() => {
        // Swiper
        new Swiper('.swiper-container', {
          autoplay: true,
          // 
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          }
        })
      }, 100) */

      //  DOM 
      this.$nextTick(() => {
        // , 
        new Swiper('.swiper-container', {
          autoplay: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          }
        })

        // new BScroll('.miste-content-wrapper', {
        //   click: true
        // })
      })
    }
  },
  // mountedswiper
  mounted () {
    // Action.js
    this.$store.dispatch('getCategorys')
    this.$store.dispatch('getShops')
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixins.styl"
  .msite  //
    width 100%
    .header
      background-color #02a774
      position fixed
      z-index 100
      left 0
      top 0
      width 100%
      height 45px
      .header_search
        position absolute
        left 15px
        top 50%
        transform translateY(-50%)
        width 10%
        height 50%
        .icon-sousuo
          font-size 25px
          color #fff
      .header_title
        position absolute
        top 50%
        left 50%
        transform translate(-50%, -50%)
        width 50%
        color #fff
        text-align center
        .header_title_text
          font-size 20px
          color #fff
          display block
      .header_login
        font-size 14px
        color #fff
        position absolute
        right 15px
        top 50%
        transform translateY(-50%)
        .header_login_text
          color #fff
    .msite_nav
      bottom-border-1px(#e4e4e4)
      margin-top 45px
      height 200px
      background #fff
      .swiper-container
        width 100%
        height 100%
        .swiper-wrapper
          width 100%
          height 100%
          .swiper-slide
            display flex
            justify-content center
            align-items flex-start
            flex-wrap wrap
            .link_to_food
              width 25%
              .food_container
                display block
                width 100%
                text-align center
                padding-bottom 10px
                font-size 0
                img
                  display inline-block
                  width 50px
                  height 50px
              span
                display block
                width 100%
                text-align center
                font-size 13px
                color #666
        .swiper-pagination
          >span.swiper-pagination-bullet-active
            background #02a774
    .msite_shop_list
            top-border-1px(#e4e4e4)
            margin-top 10px
            background #fff
            .shop_header
              padding 10px 10px 0
              .shop_icon
                margin-left 5px
                color #999
              .shop_header_title
                color #999
                font-size 14px
                line-height 20px
</style>



